.AnswerItem{
 border: 1px solid #fff;
    border-radius: 5px;
    padding: 5px 10px;
    margin-bottom: 5px;
    cursor: pointer;
}
.AnswerItem:hover{
    background-color: rgba(255,255,255,0.2);
    transition: background .3s ease-in-out;
}
.AnswerItem.success{
    border: 1px solid #fff;
    border-radius: 5px;
    padding: 5px 10px;
    margin-bottom: 5px;
    cursor: pointer;
    background: rgba(161,240,69, 0.7);
    display: block;
}
.AnswerItem.error{
    border: 1px solid #fff;
    border-radius: 5px;
    padding: 5px 10px;
    margin-bottom: 5px;
    cursor: pointer;
    background: rgba(240,87,108, 0.7);
}