.FinishedQuiz{
   padding: 20px;
   color: #fff;
    border: 2px solid #fff;
    border-radius: 5px;
    box-sizing: border-box;
    margin:0 10px;
}
.FinishedQuiz ul{
    list-style: none;
    padding: 0;
    margin: 0;
}

.primary2{
  background: #2884f6;
    color: #fff;
    display: inline-block;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid #ccc;
    color: #000;
    margin-right: 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
}
.success2{
    background: rgba(161,240,69, 0.7);
    display: inline-block;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid #ccc;
    color: #000;
    margin-right: 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;

}