.Layout {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.Layout main {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}
.MenuToggle{
   position: fixed;
    top:40px;
    left: 40px;
    font-size: 20px;
    cursor: pointer;
    color: #fff;
    -webkit-transition: opacity,left 0.22s ease-in;
    transition: opacity,left 0.22s ease-in;
    z-index: 100;
}
.MenuToggle:hover{
    opacity: 0.7;
}
.open{
    left:320px;
}
.Drawer{
     position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: 80%;
    max-width: 300px;
    padding: 20px 10px;
    box-sizing: border-box;
    background: #fff;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    -webkit-transition: -webkit-transform 0.22s ease-in;
    transition: -webkit-transform 0.22s ease-in;
    transition: transform 0.22s ease-in;
    transition: transform 0.22s ease-in, -webkit-transform 0.22s ease-in;
    z-index: 90;
}
.Drawer.close{
    -webkit-transform: translateX(-300px);
            transform: translateX(-300px);
}
.Drawer ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
.Drawer ul li{
    margin-bottom: 15px;
}
.Drawer ul li a{
    color: #363d54;
    font-size: 30px;
    text-decoration: none;
    background-color: #fff;
    position: relative;
    padding: 0 20px 10px 20px;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
}
.Drawer ul li a:hover, .active{
    opacity: 0.7;
}
.Backdrop{
    z-index: 50;
    background: rgba(0,0,0, 0.7);
    position: fixed;
    top:0;
    left: 0;
    bottom: 0;
    right: 0;
}
